.contact_banner{padding: 256px 0px 118px;background-color: #000630;background-repeat: no-repeat;background-size: cover;position: relative;overflow: hidden;}
/* .contact_banner .video{position: absolute;left: 0;top:0;width: 100%;height: 100%;object-fit: cover;} */
.contact_banner .video { position: absolute; right: 100px; top: 0; width: 700px; height: 620px; object-fit: cover;}
.contact_banner .bg{z-index: 1;}
.contact_banner .wrapper{align-items: flex-end;}
.contact_banner h1{font-size: 60px;margin-bottom: 13px;}
.contact_banner p{font-weight: 400;margin-bottom: 34px;}
.contact_banner .left_col{width: fit-content;}
.contact_banner .right_col{width: fit-content;}
.contact_banner .project_colanimate{width: 184px;min-height: 80px; padding: 6px 18px;text-align: right;    display: flex; justify-content: flex-end; align-items: center;}
.contact_banner .project_colanimate:not(:last-child){margin-right: 26px;}
.contact_banner .project_colanimate h3 { font-size: 31px; line-height: normal; margin-bottom: 0px; }
.contact_banner .project_colanimate h4 { font-size: 16px; line-height: 18px; }

.are_you_dev{padding: 63px 0px;}
.are_you_dev p{font-size: 22px;line-height: 46px;color: var(--black);font-weight: bold;}


@media only screen and (max-width:1750px) {
    .contact_banner .video { right: 10px; }
}
@media only screen and (max-width:1399px) {
    .contact_banner h1 { font-size: 50px; }
    .contact_banner p { margin-bottom: 20px; }
    .contact_banner { padding: 210px 0px 100px; }
}
@media only screen and (max-width:1279px) {
    .contact_banner .project_colanimate:not(:last-child) { margin-right: 10px; }
    .contact_banner .project_colanimate h3 { font-size: 24px; }
    .contact_banner .project_colanimate h4 { font-size: 14px; line-height: 16px; }
    .contact_banner .project_colanimate { width: 160px; }
    .contact_banner .video { width: 520px; height: 520px; }

}
@media only screen and (max-width:1023px) {
    .contact_banner .right_col { margin-top: 30px; }
    .contact_banner { padding: 190px 0px 40px; }
    .are_you_dev { padding: 45px 0px; }
    .contact_banner .bg { z-index: 1; background-position: 100% center; }

    
}
@media only screen and (max-width:767px) {
    .client_plateform { max-width: 223px; position: relative; margin: 0 auto; margin-left: 0; }
    .contact_banner h1 { font-size: 36px; }
    .contact_banner .right_col { margin-top: 30px; max-width: 320px; }
    .contact_banner .project_colanimate { width: calc(50% - 5px) !important; margin: 0 !important; min-height: 62px; padding: 5px 15px;margin-bottom: 10px !important; }
    .contact_banner .project_colanimate h4 { font-size: 12px; line-height: 14px; }
    .contact_banner { padding: 140px 0px 30px; }
    .are_you_dev { padding: 30px 0px; text-align: center; }
    .are_you_dev h2{font-size: 27px;margin-bottom: 5px;}
    .are_you_dev p { font-size: 14px; line-height: 20px; margin-bottom: 16px; }
    .are_you_dev .btn{margin: 0 auto;padding: 0px 8px 0px 15px;}
}