.industries_lider{background-color: var(--lightblue);padding: 109px 0 70.7px;}
.industries_lider .wrapper {margin-bottom: 17px;}
.industries_lider .wrapper h2{width: 364px;border-right:2px solid var(--lightgray);line-height: 74px;}
.industries_lider .wrapper p{width:calc(100% - 364px);padding-left: 50px;color: var(--darktheme);}
.industries_lider .inner{max-width: calc(100% - 159px);margin: 0 auto;margin-right: 0;}
.industries_lider .colin{display: inline-block;padding: 15px;}
.industries_lider .col{width: 514px;padding: 16px;border-radius: 20px;box-shadow: 0px 2px 15px #0000001f;background-color: var(--white);}
.industries_lider .col .img{position: relative;border-radius: 4px;overflow: hidden;min-height: 245px;margin-bottom: 15px;}
.industries_lider .col .img::after{content:'';position: absolute;right: -2px;top:-2px;width: 30px;height: 30px;background-color: var(--white);clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 50%);}
.industries_lider .col .img .icon_link{position: absolute;width: 36px;height: 36px;border-radius: 50%;left: 9px;top: 9px;background-color: var(--white);display: flex; justify-content: center; align-items: center;}
.industries_lider .col .img .icon_link:hover{transform: scale(1.1);}
.industries_lider .col h3{font-size: 30px;line-height: 40px;text-transform: uppercase;margin-bottom: 15px;transition: 0.2s ease all;}
.industries_lider .col:hover h3{color:var(--Purple);}
.industries_lider .col p{color: var(--darktheme);font-size: 16px;line-height: 22px;    overflow: hidden; display: -webkit-box; -webkit-line-clamp: 3; line-clamp: 3; -webkit-box-orient: vertical;}
.industries_lider .col .btn.btnarrow{line-height: 30px;margin-top: 10px;margin-bottom: 12px;}
.industries_lider .owl-carousel .owl-item img{width: auto;}
.industries_lider .owl-dots { display: none; }
.industries_lider .nextprev_sec{justify-content: flex-end;margin-right: 53.71px;margin-top: 33px;}
.industries_lider .nextprev_sec { justify-content: flex-end; margin-right: 53.71px;margin-top: 33px;}
.industries_lider .nextprev_sec>div{width: 59px;height: 59px;border-radius:50%;border: 1px solid var(--black);display: flex;justify-content: center;align-items: center;cursor: pointer;transition: 0.2s ease all;background-color: var(--lightblue);}
.industries_lider .nextprev_sec>div.hide{display: none;}
.industries_lider .nextprev_sec>div:hover{background-color: var(--black);}
.industries_lider .nextprev_sec>div:hover img{filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(5675%) hue-rotate(246deg) brightness(107%) contrast(101%);}
.industries_lider .nextprev_sec>div.next{transform: scale(-1);margin-left: 17.88px;}

.industries_lider .owl-carousel{width: calc(100% + 30px); margin-left: -30px;}



@media only screen and (max-width: 1750px) {
  .industries_lider .inner { max-width: calc(100% - 20px); }
  .industries_lider .col { width: 460px; }
  .industries_lider .owl-carousel { margin-left: 0; width:100%;}
}
@media only screen and (max-width: 1599px) {
    .industries_lider .col { width: 420px; box-shadow: 0px 2px 10px #0000001f;}
    .industries_lider .col h3 { font-size: 28px; line-height: 1; }
    .industries_lider .colin { padding: 10px; }

}
@media only screen and (max-width: 1399px) {
    .industries_lider .col .img { min-height: 210px; }
    .industries_lider .col { width: 380px; }


}
@media only screen and (max-width: 1365px) {

}
@media only screen and (max-width: 1279px) {
    .industries_lider .wrapper h2 { width: 300px; }
    .industries_lider .wrapper p { width: calc(100% - 300px); padding-left: 20px; }
    .industries_lider .owl-carousel { margin-left: -10px; width:calc(100% + 10px);}

}
@media only screen and (max-width: 1023px) {
    .industries_lider .nextprev_sec { margin-right: 20px; margin-top: 25px; }
    .industries_lider .nextprev_sec>div { width: 45px; height: 45px; }
    .industries_lider .nextprev_sec>div img { max-width: 20px; }
    .industries_lider .nextprev_sec>div.next { font-size: 16px; line-height: 45px; margin-left: 10px; }
    .industries_lider { padding: 60px 0; }
    .industries_lider .col h3{font-size: 20px;}


}
@media only screen and (max-width: 767px) {
    .industries_lider { padding: 30px 0; }
    .industries_lider .wrapper h2 { width: 100%; border-right:none; line-height: 1; margin-bottom: 10px; }
    .industries_lider .wrapper p { width: 100%; padding-left: 0; }
    .industries_lider .col { padding: 10px; border-radius: 10px; width: 100%; }
    .industries_lider .inner { max-width: 100%; }
    .industries_lider .col .img { min-height: 150px; }
    .industries_lider .col h3 { font-size: 20px;margin-bottom: 10px;}
    .industries_lider .col p { font-size: 13px; line-height: 20px; }
    .industries_lider .colin { padding: 10px 20px;width: 100%;display: block; }
    .industries_lider .owl-carousel { margin-left: 0; width:100%; }
    .industries_lider .nextprev_sec { margin-right: 0; margin-top: 10px; justify-content: flex-start; margin-left: 20px; } 
    .industries_lider .nextprev_sec>div { width: 35px; height: 35px; }


}