.socialmedia_life{padding: 100px 0px;}
.socialmedia_life .wrapper{max-width: 1330px;}
.socialmedia_life h2{font-size: 44px;line-height: 1.3;text-align: center;margin-bottom: 30px;}
.socialmedia_life h2 strong{background: #4396EA;
    background: linear-gradient(to bottom, #4396EA 0%, #8A5CD0 100%); -webkit-background-clip: text; -webkit-text-fill-color: transparent;}
.socialmedia_life .inner{justify-content: flex-start;}
.socialmedia_life .col{margin-top: 20px;margin-right: 1.6%;width: 23.8%;}
.socialmedia_life .col a{    display: block; background-color: var(--lightblue);}
.socialmedia_life .col img { display: block; object-fit: cover; margin: 0 auto}
.socialmedia_life .col:nth-child(4n){margin-right: 0px;}

@media only screen and (max-width: 1279px) {
    .socialmedia_life h2 { font-size: 35px; }
    
}
@media only screen and (max-width:1023px) {
    .socialmedia_life h2 { font-size: 35px; margin-bottom: 10px; }
    .socialmedia_life { padding: 60px 0px; }
}
@media only screen and (max-width:767px) {
    .socialmedia_life h2 { font-size: 28px; margin-bottom: 0; }
    .socialmedia_life { padding: 40px 0px; }
    .socialmedia_life .col { margin-top: 4%; margin-right: 4%; width:48%; }
    .socialmedia_life .col:nth-child(4n) { margin-right:  4%; }
    .socialmedia_life .col:nth-child(2n) { margin-right: 0px; }

}