.project_contries{padding: 100px 0;background-color:var(--black);}
.project_contries h2{color: var(--white);text-align: center;font-size: 40px;line-height: 50px;margin-bottom: 36px;}
.project_contries .inner { width: 100%;max-width: 1184px;margin: 0 auto; }
.contries_title{border-top: 1px solid #757575;border-bottom: 1px solid #757575;padding: 22px 60px;}
.contries_title .col{padding: 6px 10px 9px 10px;border-radius:15px;text-align: center;transition: 0.2s ease all;cursor: pointer;}
.contries_title .col span{display: block;height: 35px;}
.contries_title .col span img{max-height: 35px;max-width: max-content;    margin: 0 auto;}
.safari .contries_title .col span img{max-width: 25px !important;}

.contries_title .col h3{font-size: 18px;line-height: 30px;font-weight: 600;color:#676767;transition: 0.2s ease all;    white-space: nowrap;}
.contries_title .center .col,
.contries_title .col:hover {    background: rgb(132, 98, 211); background: linear-gradient(0deg, rgba(132, 98, 211, 1) 3%, rgba(67, 150, 234, 1) 100%);}
.contries_title .center .col h3{color: var(--white);}
.contries_title .col:hover h3{color: var(--white);}

.contries_wrap{position: relative;}
.contries_wrap::after,.contries_wrap::before{content:'';position: absolute;width:49px;height: calc(100% + 2px);background-repeat: no-repeat;background-size: 100% 100%;top: -1px;right:-30px;background-image: url(../../public/images/ss_layer.png);z-index: 1;}
.contries_wrap::before{right: auto;left: -30px;transform: scale(-1);}
.contries_contain .owl-item { max-height: 0; opacity: 0; }
.contries_contain .owl-item.active { max-height: max-content; opacity: 1;}

.contries_contain .colin {align-items: normal;display: flex;}
.contries_contain .colin .left_col{width: 48%;padding:40px 37px 0px 0px;border-right: 1px solid #757575;}
.contries_contain .colin .right_col{width: 52%;padding:40px 0px 0px 37px;}
.contries_contain .colin .img{width: 35px;height: 35px;border-radius: 50%;overflow: hidden;}
.contries_contain .colin .left_col p{width: calc(100% - 35px);padding-left: 18px;font-size: 14px;line-height: 20px;}
.contries_contain .colin .map{width: 100%;margin-top: 19px;text-align: center;opacity: 0;transition: 1s ease all;}
.contries_contain .owl-item.active .colin .map{opacity: 1;}
.contries_contain .colin .right_col h4{font-size: 14px;line-height: 20px;color: var(--white);width: 50%;font-weight: 500;}
.contries_contain .colin .right_col p{font-size: 14px;line-height: 20px;color: var(--white);width: 49%;}
.contries_contain .coltext{margin-bottom: 20px;}
.contries_contain .owl-carousel .owl-item img { display: block; width: auto; margin: 0 auto; }

.project_contries .nextprev_sec {width: 100%;justify-content: center;margin-bottom: 16px;}
.project_contries .nextprev_sec>div{width: 48px;height: 48px;border-radius:50%;border: 1px solid var(--white);display: flex;justify-content: center;align-items: center;cursor: pointer;transition: 0.2s ease all;}
.project_contries .nextprev_sec>div.hide{opacity: 0;transform: scale(0) !important;}
.project_contries .nextprev_sec>div img{filter: brightness(0) saturate(100%) invert(99%) sepia(55%) saturate(2%) hue-rotate(196deg) brightness(118%) contrast(100%);}
.project_contries .nextprev_sec>div:hover{border: none;background-color: var(--white);}
.project_contries .nextprev_sec>div:hover img { filter: none; }
.project_contries .nextprev_sec>div.next{transform: scale(-1);margin-left: 17.88px;} 
.contries_title { flex-wrap: nowrap; overflow-x: auto; overflow-y: hidden; }
.contries_title::-webkit-scrollbar { height: 2px; }
.contries_title::-webkit-scrollbar-thumb { background-color: #888; border-radius: 4px; }
.contries_title::-webkit-scrollbar-track { background-color: #eee; }
body .project_contries .owl-carousel.owl-theme .owl-nav{display: none !important;}


@media only screen and (max-width:1279px) {
    .contries_title .col h3 { font-size: 18px; line-height: 25px; }
    .contries_contain .colin .left_col { padding: 40px 15px 0px 0px; }
    .contries_contain .colin .right_col { padding: 40px 0px 0px 15px; }
    .project_contries { padding: 60px 0; }
}
@media only screen and (max-width:1023px) {
    .project_contries h2 { font-size: 35px; line-height: 42px; margin-bottom: 25px; }
    .contries_contain .colin .right_col h4 { font-size: 14px; line-height: 20px; width: 100%; margin-bottom: 5px; }
    .contries_contain .colin .right_col p { font-size: 14px; line-height: 20px; width: 100%; }
}
@media only screen and (max-width:767px) {
    .project_contries { padding: 30px 0; }
    .project_contries h2 { font-size: 24px; line-height: 34px; margin-bottom: 22px; }
    .project_contries .nextprev_sec>div { width: 38px; height: 38px; }
    .project_contries .nextprev_sec>div img{width: 11px;}
    .contries_contain .colin .left_col { padding: 0; padding-top: 20px;width: 100%;border: none;}
    .contries_contain .colin .right_col { padding: 0; margin-top: 15px; width: 100%; }
    .contries_contain .colin .right_col p { font-size: 12px; line-height: 18px; width: 100%; }
    .contries_contain .coltext { margin-bottom: 10px; }
    .contries_contain .colin .left_col p { padding-left: 10px; font-size: 12px; line-height: 18px;}
    .contries_title { padding: 12px 20px; }
    .contries_wrap::after, .contries_wrap::before { width: 10px }
    .contries_title .col h3 { font-size: 12px; line-height: 16px; }
   .contries_contain .owl-carousel .owl-item .map img { display: block; width: auto; margin: 0 auto; max-width: 150px; }
   
}