.why_choose_companydesign{background-color: var(--black);padding: 100px 0px;}
.why_choose_companydesign .top_col{margin-bottom: 104px;}
.why_choose_companydesign .top_col h2{color: var(--white);max-width: 692px;    font-size: 40px; line-height: 1.3;}
.why_choose_companydesign .top_col p{max-width: 557px;font-size: 22px;line-height: 32px;}
.why_choose_companydesign .top_col p span{color: var(--Purple);}
.why_choose_companydesign .col{width: 25%;}
.why_choose_companydesign .col span{display: block;margin-bottom: 30px;}
.why_choose_companydesign .col h3{font-size: 24px;color: var(--white);font-weight: bold;line-height: 33px;margin-bottom: 10px;}
.why_choose_companydesign .col p{line-height: 27px;max-width: 315px;}
@media only screen and (max-width: 1750px) {
    .why_choose_companydesign { padding: 80px 0px; }
}
@media only screen and (max-width:1279px) {
    .why_choose_companydesign { padding: 50px 0px; }
    .why_choose_companydesign .top_col h2,.why_choose_companydesign .top_col p{width: 50%;}
    .why_choose_companydesign .top_col p { font-size: 20px; line-height: 30px; }
    .why_choose_companydesign .col { padding-right: 10px; }
    .why_choose_companydesign .top_col { margin-bottom: 70px; }
    .why_choose_companydesign .col h3 { font-size: 20px; line-height: 30px; margin-bottom: 10px; }
}
@media only screen and (max-width:1023px) {
    .why_choose_companydesign .top_col h2, .why_choose_companydesign .top_col p { width: 100%; }
    .why_choose_companydesign .top_col p{margin-top: 15px;}
    .why_choose_companydesign .col { width: 50%; margin-bottom: 25px; }
}
@media only screen and (max-width:767px) {
    .why_choose_companydesign { padding: 30px 0px 0; }
    .why_choose_companydesign .top_col p { font-size: 16px; line-height: 24px; }
    .why_choose_companydesign .col p { line-height: 1.6; }
    .why_choose_companydesign .col { width: 100%; margin-bottom: 30px; }
    .why_choose_companydesign .top_col { margin-bottom: 30px; }
    .why_choose_companydesign .col span { margin-bottom: 5px; max-width: 50px; }

}