.submenu { background-color: #F5F5F5; position: fixed; width: 100%; left: 0; top: 90px;align-items: normal;box-shadow: 0px 4px 13px #00000026;opacity: 0;pointer-events: none;transition: 0.2s ease all;}
.submenu .left_col{width:506px;background-color: var(--white);padding: 50px 52px 50px 158px;}
.submenu .left_col .link{color: var(--black);font-size: 24px;line-height: 33px;font-weight: bold;margin-bottom: 10px;}
.submenu .left_col .link:hover{color: var(--Purple);}
.submenu .left_col p{color: var(--black);font-size: 16px;line-height:1.5;margin-bottom: 20px;}
.submenu .right_col{width:calc(100% - 506px);padding: 50px 158px 50px 100px;align-items: normal;}

header .menucol ul li:hover .submenu{opacity: 1;pointer-events: all;}
header .menucol ul li.drop::after { content: ''; position: absolute; left: 0; top: 100%; width: 100%; height: 35px; }
header.fixed .submenu{top: 80px;} 
header.fixed.isactive .submenu{opacity: 0 !important;pointer-events: none !important;}

.menutitle{display: none;}
.childmenu .menutitle{display: none;}
/* industrymenu */
.industrymenu{}
.industrymenu .right_col ul{width:100%;display: flex;flex-wrap: wrap;    align-content: flex-start;    gap: 5px;}
.industrymenu .right_col ul li{margin:0px;width: 24.6%;margin-bottom: 20px;}
.industrymenu .right_col ul li a{line-height: 50px;color: var(--darktheme);display: flex;width: 100%;align-items: center;
    filter: brightness(0) saturate(100%) invert(26%) sepia(4%) saturate(13%) hue-rotate(357deg) brightness(81%) contrast(84%);
    -o-filter: brightness(0) saturate(100%) invert(26%) sepia(4%) saturate(13%) hue-rotate(357deg) brightness(81%) contrast(84%);
    -webkit-filter: brightness(0) saturate(100%) invert(26%) sepia(4%) saturate(13%) hue-rotate(357deg) brightness(81%) contrast(84%);
    -moz-filter: brightness(0) saturate(100%) invert(26%) sepia(4%) saturate(13%) hue-rotate(357deg) brightness(81%) contrast(84%);
    word-break: break-all;
    line-height: 22px;
}
.industrymenu .right_col ul li a:hover{
    filter: brightness(0) saturate(100%) invert(75%) sepia(61%) saturate(7051%) hue-rotate(231deg) brightness(83%) contrast(96%);
    -o-filter: brightness(0) saturate(100%) invert(75%) sepia(61%) saturate(7051%) hue-rotate(231deg) brightness(83%) contrast(96%);
    -webkit-filter: brightness(0) saturate(100%) invert(75%) sepia(61%) saturate(7051%) hue-rotate(231deg) brightness(83%) contrast(96%);
    -moz-filter: brightness(0) saturate(100%) invert(75%) sepia(61%) saturate(7051%) hue-rotate(231deg) brightness(83%) contrast(96%);
}
.industrymenu .right_col ul li a span{width: 50px;height: 50px;border: 1px solid  var(--darktheme);display: flex;align-items: center;justify-content: center;border-radius: 50%;margin-right: 15px;    min-width: 50px;}
.industrymenu .right_col .img{width: 360px;margin-left: 30px;display: none;}
.industrymenu .right_col .img p{color: var(--black);font-size: 16px;line-height: 1.5;margin-top: 15px;}

/* servicesmenu */
.servicesmenu{}
header .servicesmenu .left_col .link{margin-bottom: 30px;}
header .servicesmenu .left_col ul{justify-content: flex-start;}
header .servicesmenu .left_col ul li { margin: 0px 27px 25px 0; }
header .servicesmenu .left_col ul li a:hover{transform: scale(1.1);}
header .servicesmenu .right_col{justify-content:flex-start;align-items: normal;}
header .servicesmenu .right_col .colin { width: 25%; padding-right: 15px; }
header .servicesmenu .colin a:hover{filter: brightness(0) saturate(100%) invert(75%) sepia(61%) saturate(7051%) hue-rotate(231deg) brightness(83%) contrast(96%);}
header .servicesmenu .colin h3{margin-bottom: 30px;}
header .servicesmenu .colin h3 a{color: var(--black);font-size: 20px;font-weight: 600;}
header .servicesmenu .colin ul li{margin: 0;margin-bottom: 10px;}
header .servicesmenu .colin ul li a{color: var(--darktheme);font-weight: 400;line-height: 30px;}

header .servicesmenu .colin .drop-icon{display: none;}

@media only screen and (max-width: 1750px) {
    .submenu .left_col { width: 387px; padding: 50px 52px 50px 40px; }
    .submenu .right_col { width: calc(100% - 387px); padding: 50px 40px 50px 40px; }

}
@media only screen and (max-width: 1599px) {
    header .servicesmenu .colin h3{margin-bottom: 20px;}
    header .servicesmenu .colin h3 a { font-size: 18px; }
    header .servicesmenu .colin ul li a{font-size: 16px;}
    .industrymenu .right_col ul li{width:23.88%;margin: 0 0 10px;}
    .industrymenu .right_col ul { height: 61vh; overflow: auto; }

}
@media only screen and (max-width: 1399px) {
    .submenu .left_col { width: 350px; padding: 40px; }
    .submenu .right_col { width: calc(100% - 350px); padding: 40px; }
    .industrymenu .right_col .img { width: 260px; margin-left: 20px; }    
    .industrymenu .right_col ul li a span { width: 45px; height: 45px; margin-right: 10px;    min-width: 45px;}
    .industrymenu .right_col ul li a span img{max-width: 26px;}

    header .servicesmenu .left_col ul li { margin: 0px 22px 25px 0; }
    header .servicesmenu .colin h3 a { font-size: 16px; }
    header .servicesmenu .colin ul li a { font-size: 15px; }
    header .servicesmenu .colin ul li { margin: 0; margin-bottom: 5px; }

}

@media only screen and (max-width: 1279px) {
    
    .submenu { background-color: var(--black); width: 400px; left: auto; top: 54px !important; box-shadow: none; opacity: 1 !important; pointer-events: all !important; right: 0; z-index: 99; padding: 0px 20px; height: calc(100vh - 160px); display: block; padding-top: 60px; transform: translateX(100%);}
    .submenu.slide-open{transform: translateX(0);}
    header.fixed.isactive .submenu { opacity: 1 !important; pointer-events: all !important; }
    .submenu .left_col { width: 100%; padding:0;background-color: var(--black); }
    .submenu .right_col { width: 100%; padding:0;order: -1; }
    .submenu .left_col { border-bottom: 1px solid #fff;}
    .submenu .left_col .link { color: var(--white); font-size: 18px; line-height: 39px; font-weight: 600; margin-bottom: 0px; }
    
/* industrymenu */
 .submenu.industrymenu .left_col p,.submenu.industrymenu .left_col h4{display: none !important;}
 .industrymenu .right_col .img{display: none;}
 .industrymenu .right_col ul { width: 100%; padding: 0; }
 .industrymenu .right_col ul li { margin: 0px; width: 100%; margin-bottom: 0; border-bottom: 1px solid #fff;}
 .industrymenu .right_col ul li a { color: var(--white) !important; line-height: 40px;filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(2%) hue-rotate(166deg) brightness(109%) contrast(101%);}
 .industrymenu .right_col ul li a span{display: none;}
 header .menucol ul li.drop::after{display: none !important;}

 .menutitle{display:block;text-align: center;position: absolute;top: 20px;left: 0;color: var(--white);width: 100%;font-size: 18px;}
 .menutitle .back{position: absolute;left: 20px;top: 2px;color: var(--Purple);font-weight: bold;font-size: 16px;}


/* servicesmenu */
header .servicesmenu .left_col .link { margin-bottom: 0;}
header .servicesmenu .left_col ul{display: none;}
header .servicesmenu .right_col { display: block; }
header .servicesmenu .colin h3 { margin-bottom: 0; border-bottom: 1px solid var(--white);position: relative;}
header .servicesmenu .colin .drop-icon { top: 0; bottom: 0; margin: auto; display: block;}
header .servicesmenu .colin h3 a{line-height: 39px;color: var(--white); font-size: 18px; font-weight: 600; }
header .servicesmenu .colin ul { display: block; padding: 0; }
header .servicesmenu .colin ul li{margin-bottom: 0px;border-bottom: 1px solid var(--white);}
header .servicesmenu .colin ul li a{color: var(--white); font-size: 18px; line-height: 39px; font-weight: 600; }
header .servicesmenu .childmenu { position: fixed; right: 0; width: 400px; padding: 60px 20px 0; top:0; background-color: var(--black); height: calc(100vh - 140px); z-index: 9; transform: translateX(100%);transition: 0.2s ease all;}
header .servicesmenu  .childmenu .menutitle{display: block;}
header .servicesmenu  .childmenu.active { transform: translateX(0);}
header .servicesmenu .right_col .colin { padding-right:0; width: 100%; }



}
@media only screen and (max-width:1023px) {
    header .servicesmenu .colin h3 a,.submenu .left_col .link,header .servicesmenu .colin ul li a ,.menutitle{ font-size: 16px; }

}
@media only screen and (max-width:767px) {
    .submenu,header .servicesmenu .childmenu{width: 100%;}
    .menutitle .back{top: 0;}
    
    
}
@media only screen and (max-width:374px) {
    .menutitle .back { font-size: 13px; top: -1px; }
    
}