.services_banner{padding-top: 347px;background-image: url(../../public/images/services_bg.jpg);background-repeat: no-repeat;background-size: cover;position: relative;;}
.services_banner .video{position: absolute;left: 0;top:0;width: 100%;height:100%;object-fit: cover;}
.services_banner .inner h1{margin-bottom: 27px;}
.services_banner .inner p{max-width: 589px;}
.services_banner .awards_logo{padding: 0px;padding-bottom: 54px;margin-top:225px;background-image: none;}

@media only screen and (max-width: 1750px) {
    .services_banner { padding-top: 290px; } 
    .services_banner .awards_logo { padding-bottom: 54px; margin-top: 125px; }
}
@media only screen and (max-width:1399px) {
    .services_banner { padding-top: 250px; }
    .services_banner .awards_logo { padding-bottom: 40px; margin-top: 85px; }
}
@media only screen and (max-width:767px) {
    .services_banner { padding-top: 120px; }
    .services_banner .inner h1 { margin-bottom: 20px; }
    .services_banner .awards_logo { padding-bottom: 20px; margin-top: 20px; }
    .services_banner .awards_logo li { margin-bottom: 10px; }

}