.blog_section{}
.blog_section .wrapper{border-top:1px dashed var(--black); padding-top: 50px;}
.blog_row{margin:0 -44px; display: flex;}
.blog_row:last-child{margin-bottom: 130px;}
.blog_row:nth-child(odd) .blog_list:last-child{border: 0;}
.blog_row:nth-child(even) .blog_list:first-child{border: 0;}
.blog_row:nth-child(even){flex-direction: row-reverse;}
.blog_row .blog_list{width:50%;padding:0 44px 50px;border-right:1px dashed var(--black);}
.blog_row .blog_list .blog_img{border-radius:20px;line-height:0;overflow:hidden;display: block;transition: 0.2s ease all;}
/* .blog_row .blog_list .blog_img:hover{box-shadow: 0px 0px 10px #00000091;} */
.blog_row .blog_list .blog_img img{width: 100%;}
.blog_row .blog_list .blog-content .blog_info{margin:22px 0 17px;}
.blog_row .blog_list .blog-content h3 a{color:var(--black);font-size:40px;font-weight:800;text-transform:uppercase;}
.blog_row .blog_list .blog-content h3 a:hover{color: var(--Purple);}
.blog_row .blog_list .blog-content .col-left .btnmix{border:1px solid var(--Purple);color:var(--white);}
.blog_row .blog_list .blog-content .col-left .btnmix:hover{border-color:#9b66fd;background-color: #9b66fd;}
.blog_row .blog_list .blog-content .col-left .btnmix::after{display:none;}
.blog_row .blog_list .blog-content .date{display:flex;align-items:center;margin-left:14px;}
.blog_row .blog_list .blog-content .date img{margin-right:10px;}
.blog_row .blog_list .blog-content .col-right label{justify-content:flex-start;color:var(--darktheme);font-size:18px;font-weight:500;}
.blog_row .blog_list .blog-content .col-right label img{margin-right:7px;}
.blog_row .blog_list .blog-content .col-right label a{color:var(--darktheme);font-size:18px;font-weight:bold;margin-left:5px;}
.blog_row .blog_list .blog-content .col-right label span{position:relative;padding-left:14px;margin-left:14px;}
.blog_row .blog_list .blog-content .col-right label span::after{width:5px;height:5px;border-radius:50%;content:'';left:0;top:0;bottom:0;margin:auto;background:rgb(132,98,211);background:linear-gradient(90deg,rgba(132,98,211,1) 3%,rgba(67,150,234,1) 100%);position:absolute;}


.blog_row .blog_list .blog_small{display:flex;align-items:flex-start;}
.blog_row .blog_list .blog_small:not(:last-child){margin-bottom:32px;}
.blog_row .blog_list .blog_small .blog_img{width:46%;border-radius:10px;}
.blog_row .blog_list .blog_small .blog-content{width:54%;padding:20px 0 20px 30px;}
.blog_row .blog_list .blog_small .blog-content .blog_info{margin:0 0 15px;}
.blog_row .blog_list .blog_small .blog-content .date{font-size: 14px;}
.blog_row .blog_list .blog_small .blog-content .date img{width: 14px;}
.blog_row .blog_list .blog_small .blog-content h3{margin-bottom:10px;}
.blog_row .blog_list .blog_small .blog-content h3 a{font-size:25px;}
.blog_row .blog_list .blog_small .blog-content .col-right label,.blog_row .blog_list .blog_small .blog-content .col-right label a{font-size: 14px;}
.blog_row .blog_list .blog_small .blog-content .col-right label img{width: 27px;}


.loader_blog{width: 100%;text-align: center;margin:80px 0px;}
.loader_blog img{animation: rotate-animation .8s linear infinite;}

@media only screen and (max-width: 1750px){
    .blog_row{margin: 0 -25px;}
    .blog_row .blog_list{padding:0 25px 50px;}

}
@media only screen and (max-width:1599px) {
    .blog_row .blog_list .blog-content h3 a{font-size:32px;}
    .blog_row .blog_list .blog_small .blog-content h3 a{font-size:22px;}

}
@media only screen and (max-width:1279px){
    .blog_row .blog_list .blog_small .blog_img{width:40%;}
    .blog_row .blog_list .blog_small .blog-content{width:60%;padding:0 0 0 20px;}
    .blog_row .blog_list .blog-content .blog_info .col-left{margin-bottom: 10px;}
    .blog_row .blog_list .blog-content h3 a{font-size:26px;}
    .blog_row .blog_list .blog_small .blog-content h3 a{font-size:18px;}
    .blog_row .blog_list .blog_small .blog-content .blog_info{margin:0 0 0;}
    .blog_row { margin: 0 -20px;    flex-wrap: wrap;}
    .blog_row .blog_list { width: 100%; }
    
}
@media only screen and (max-width:767px){
    .blog_section .wrapper{padding-top: 30px;}
    .blog_row{flex-wrap: wrap;}
    .blog_row .blog_list{width: 100%; border-right: 0;}
    .blog_row .blog_list{padding:0px 20px 25px;}
    .blog_row .blog_list .blog_img{border-radius:12px;}
    .blog_row .blog_list .blog-content .col-right label a,.blog_row .blog_list .blog-content .col-right label{font-size: 14px;}
    .blog_row .blog_list .blog-content h3 a{font-size:20px;}
    .blog_row .blog_list .blog_small{flex-wrap: wrap;}
    .blog_row .blog_list .blog_small .blog_img,.blog_row .blog_list .blog_small .blog-content{width: 100%;}
    .blog_row .blog_list .blog_small .blog-content{padding: 22px 0 0 0;}
    .blog_row .blog_list .blog_small:not(:last-child) { margin-bottom: 25px;}
    .blog_row:last-child { margin-bottom: 0; }
    nav .pagination { margin: 0px 0 25px; }
}