.casestuding_banner{background-color: #000;position: relative;overflow: hidden;}
.casestuding_banner:after { content: ''; position: absolute; left: 0; top: 0; width: 100%; height: 100%; box-shadow: 1140px 870px 690px inset #000000bf; }
.casestuding_banner .video{    position: absolute; left: 0; top: 0; width: 100%; height: 100%; object-fit: cover;transition: top 2s ease, left 2s ease;transform: scale(2);transform-origin: center;animation: moveVideo 20s infinite alternate linear; }

/* Define the keyframes for the movement */
@keyframes moveVideo {
  0% {
    transform: translate(0, 0) scale(1.5);
  }
  25% {
    transform: translate(calc(40vw - 50%), 0) scale(1.5); /* Move to the right edge */
  }
  50% {
    transform: translate(calc(40vw - 15%), calc(-10vh - 10%)) scale(1.5); /* Move to the bottom right corner */
  }
  75% {
    transform: translate(0, calc(50vh - 15%)) scale(1.5); /* Move to the bottom left corner */
  }
  100% {
    transform: translate(0, 0) scale(1.5); /* Move back to the original position */
  }
}


.casestuding_banner h1{margin-bottom: 20px;}
.casestuding_banner .wrapper{padding-top: 204px;}
.casestuding_banner .wrap{margin: 26px 0 0;}
.casestuding_banner .wrap .selectcol:nth-child(2){display: none !important;}
.casestuding_banner .awards_logo { margin-top: 0; padding: 92px 0px 32px; background-image: none; }
.selectcol{height: 49px;background-color: #00000085;margin-right: 12px;position: relative; max-width: 400px; min-width: 219px;    border-radius: 12px;}
.selectcol h3{font-size: 10px;font-weight: 500;color: var(--white);position: absolute;left: 10px;top: 5px;z-index: 2;}
.select2{width: 100% !important;}

body .select2-container--default .select2-selection--single { background-color: transparent; border: 1px solid var(--white); border-radius: 10px !important; height: 49px; width: 100%; padding-top: 15px; font-size: 18px; color: var(--white); font-weight: bold; opacity: 1;padding-right: 50px;}
body .select2-container--default .select2-selection--single .select2-selection__rendered { color: var(--white); padding: 0px 10px; }
body .select2-container--default .select2-selection--single .select2-selection__placeholder{color: var(--white);}
body .select2-container--default .select2-selection--single .select2-selection__arrow b{display: none;}
body .select2-container--default .select2-selection--single .select2-selection__arrow {height: 100%;background-image: url(../../public/images/select.png);background-repeat: no-repeat;background-position: center;  right: 22px;}
body .select2-container--default .select2-selection--single[aria-expanded="true"] .select2-selection__arrow {transform: scale(-1);}
body .select2-container--default .select2-selection--single[aria-expanded="true"] {box-shadow: 0px 0px 20px #553781;border-color: #553781;}

body .select2-dropdown { background-color: #553781; border: none; border-radius: 7px !important; top: 10px; padding: 5px; }
body .select2-dropdown:after { content: ''; position: absolute; top: -5px; right: 27px; width: 10px; height: 10px; background-color: #553781; transform: rotate(45deg); }
body .select2-search--dropdown { display: none; }
body .select2-results__option { padding: 0; line-height: 30px; padding-left: 10px; font-size: 14px; font-weight: 500; color: var(--white); z-index: 1;}
body .select2-container--default .select2-results__option--highlighted[aria-selected] { background-color: #0c0416; color:var(--white); }
body .select2-container--default .select2-results__option[aria-selected=true] { background-color: #0c0416; position: relative;}
body .select2-container--default .select2-results__option[aria-selected=true]::after {content:'';    background: rgb(132, 98, 211);
  background: linear-gradient(90deg, rgba(132, 98, 211, 1) 3%, rgba(67, 150, 234, 1) 100%);width: 4px;height: 22px;top: 0;bottom: 0;left: -2px;margin: auto;position: absolute;}
body .select2-container--default .select2-results > .select2-results__options { overflow-y: unset; }
body .select2-container--default .select2-results > .select2-results__options { overflow-y: auto !important; }
/* width */
body .select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar { width: 4px; }
body .select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar-track { box-shadow: inset 0 0 5px grey; border-radius: 10px; }
body .select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar-thumb { background: rgb(132, 98, 211); border-radius: 10px; }
body .select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar-thumb:hover { background:rgb(132, 98, 211); }

.loaderdata{filter: brightness(0) saturate(100%) invert(100%) sepia(14%) saturate(0%) hue-rotate(77deg) brightness(101%) contrast(101%);
  animation: rotate-animation 0.8s linear infinite;}


@media only screen and (max-width: 1750px) {
  .casestuding_banner .awards_logo { margin-top: 0; padding: 60px 0px 30px; background-image: none; }
}
@media only screen and (max-width:1399px) {
  .casestuding_banner .awards_logo { padding: 30px 0px 30px; }
}
@media only screen and (max-width:767px) {
  .casestuding_banner h1 { margin-bottom: 20px; font-size: 30px; line-height: 1.25; }
  .casestuding_banner .wrapper { padding-top: 130px; }
  body .selectcol { width: 100%; margin-bottom: 10px;margin-right: 0px; }
  body .select2-container--default .select2-selection--single { font-size: 16px; }
  .casestuding_banner .awards_logo { padding: 15px 0px 30px; }
  

}