.industries_challenges{background-color:var(--white);padding: 120px 0px;}
.industries_challenges h2{color: var(--black);margin-bottom: 50px;}
.industries_challenges .wrap{}
.industries_challenges .wrap .col{position: relative;display: flex;justify-content: flex-start;align-items: flex-end;border-radius: 10px;overflow: hidden;padding: 25px 30px;}
.industries_challenges .wrap .col img{position: absolute;right: 0;top: 0;}
.industries_challenges .wrap .col1,.industries_challenges .wrap .col2{width: calc(25% - 20px);min-height: 720px;}
.industries_challenges .wrap .colin{width: calc(50% - 20px);min-height: 720px;}
.industries_challenges .wrap .col1{background-color: #CBEAFE;}
.industries_challenges .wrap .col2{background-color: #FFE0E2;}
.industries_challenges .wrap .col3{background-color: #FFF0E1;width: 100%;height: calc(50% - 10px);height: 350px;margin-bottom: 20px;    align-items: center;}
.industries_challenges .wrap .col4{background-color: #ECFEE7;width:calc(50% - 10px);height: 350px;}
.industries_challenges .wrap .col5{background-color: #D4F4EC;width:calc(50% - 10px);height: 350px;}
.industries_challenges .wrap .col h3{color: var(--black1);font-size: 40px;line-height: 44px;font-weight: 900;margin-bottom: 10px;}
.industries_challenges .wrap .col h3 span{color: var(--blue);}
.industries_challenges .wrap .col p{color: var(--black);font-size: 16px;line-height: 24px;}
.industries_challenges .wrap .col3 .text { max-width: 330px; }
.industries_challenges .wrap .col2 img { left: 10px; }
.industries_challenges .mid {position: absolute; width: 180px; height: 180px; border-radius: 50%; background-color: var(--white); padding: 20px; left: 0; right: 0; top: auto; bottom: calc(50% - 150px); margin: auto; }
.industries_challenges .mid .btn { width: 140px; height: 140px; border-radius: 50%; text-align: center; padding: 0; font-size: 11px; line-height: 16px; font-weight: 900; display: flex; justify-content: center; align-items: center; padding-top: 15px; text-transform: uppercase;}
.industries_challenges .mid .btn img{margin-top: 5px;}
/* .industries_challenges .mid .btn::after{ left: -26px; top: -31px; width: 130%; height: 130%; } */

@media only screen and (max-width:1750px) {
    .industries_challenges .wrap .col3 img{max-width: 53%;}
}
@media only screen and (max-width:1599px) {
    .industries_challenges .wrap .col1, .industries_challenges .wrap .col2 { width: calc(25% - 10px); min-height: 720px; }
    .industries_challenges .wrap .colin { width: calc(50% - 10px); min-height: 250px; }
    .industries_challenges .wrap .col:not(.col1,.col3) img { max-width: 80%; }
    .industries_challenges .mid { width: 170px; height: 170px; padding: 15px; bottom: calc(50% - 135px); }
}
@media only screen and (max-width:1399px) {
    .industries_challenges .wrap .col1, .industries_challenges .wrap .col2 { width: calc(25% - 5px); min-height: 600px;}
    .industries_challenges .wrap .col { padding: 15px 15px; }
    .industries_challenges .wrap .col h3 { font-size: 32px; line-height: 32px; }
    .industries_challenges .wrap .col p { color: var(--black); font-size: 16px; line-height: 20px; }
    .industries_challenges .wrap .col3{margin-bottom: 10px;}
    .industries_challenges .wrap .col3,.industries_challenges .wrap .col4,.industries_challenges .wrap .col5{height: 295px;}
    .industries_challenges .wrap .col4 { width: 50%; }
    .industries_challenges .mid { width: 150px; height: 150px; padding: 10px; bottom: calc(50% - 135px); }
    .industries_challenges .mid .btn { width: 130px; height: 130px; }
}
@media only screen and (max-width:1023px) {
    .industries_challenges { padding: 70px 0px; }
    .industries_challenges .wrap .col1, .industries_challenges .wrap .col2 { width: calc(50% - 5px); min-height: 510px; }
    .industries_challenges .wrap .colin { width: 100%; min-height: 250px; margin-top: 10px; }
    .industries_challenges .wrap .col:not(.col1,.col3) img { max-width: 60%; margin: 0 auto; }
    .industries_challenges .wrap .col1 img { max-width: 230px; }
    .industries_challenges .wrap .col1 .text,.industries_challenges .wrap .col2 .text{position: relative;bottom: 55px;}
    .industries_challenges .mid { width: 150px; height: 150px; padding: 10px; bottom: auto; top: 539px; }
}
@media only screen and (max-width:767px) {
    .industries_challenges { padding: 30px 0px 45px; }
    .industries_challenges h2 { margin-bottom: 20px; }
     body .industries_challenges .wrap .col { width: 100%; min-height: auto !important; flex-direction: column; padding: 0px !important;margin-bottom: 10px !important;height: auto !important;}
     body .industries_challenges .wrap .col img { max-width: 230px; position: relative; }
     .industries_challenges .wrap .col .text{ position: relative; bottom: 0px !important;padding: 20px !important;    width: 100%;}
     .industries_challenges .wrap .colin{margin-top: 0px !important;}
     .industries_challenges .wrap .col h3 { font-size: 24px; line-height: 30px; }
     .industries_challenges .wrap .col:not(.col1,.col3) img { max-width: 80%;margin-right: 0; margin: auto;}
     .industries_challenges .mid .btn { width: 90px; height: 90px; position: relative; }
    .industries_challenges .mid { width: 100px; height: 100px; padding: 5px; bottom: -30px; top: auto; position: absolute; right: 40px; left: auto; }



}