.banner{min-height:823px;background-color: #000;padding-top: 25vh;position: relative;}
.banner .wrapper{align-items: flex-start;}
.banner h1{margin-bottom: 32px;    letter-spacing: 2px;}
.banner .left_col{width: calc(100% - 409px);position: relative;}
.banner .right_col{width: 409px;text-align: right;    padding-top: 33px;}
.banner .bg::after{content:'';position: absolute;left: 0;bottom: 0;height: 100%;width: 100%;box-shadow: 0px -50vh 100vh inset #00000069;}
.banner .client_plateform{margin: 0 auto; margin-right: 0;margin-top: 44px; }
.banner .btn {align-items: center; border-radius: 50%; bottom: 7px; display: flex; font-size: 16px; font-weight: bold; height: 165px; justify-content: center; line-height: 20px; padding: 15px 0 0; position: absolute; right: 10%; text-align: center;width: 165px;}
.banner .btn img{margin-top: 5px;}
.banner .video{position: absolute;left: 0;top:0;width: 100%;height: 100%;object-fit: cover;}

.project_colanimate{min-height: 97.5px;max-width:223px;position: relative;    margin: 0 auto; margin-right: 0;}
.project_colanimate span{position: absolute;width: 15px;height: 15px;border: 1px solid var(--white);transition: 0.2s ease all;}
.project_colanimate span.rt::after{content:'';position: absolute;width: 15px;height: 1px;background-color: var(--white);display: block;transform: rotate(-45deg); top: 12px; right: 5px;}
.project_colanimate span.lt{top: 0;left: 0;border-right: none;border-bottom: none;}
.project_colanimate span.lb{bottom: 0;left: 0;border-right: none;border-top: none;}
.project_colanimate span.rt{top: 0;right: 0;border-bottom: none;border-left: none;}
.project_colanimate span.rb{bottom: 0;right: 0;border-left: none;border-top: none;}

.project_colanimate h3{font-size: 38px;font-weight: bold;line-height: 45px;margin-bottom: 4px;color: var(--white);}
.project_colanimate h4{font-size: 20px;line-height: 22px;font-weight: 400;color: var(--white);    white-space: nowrap;}
.project_colanimate .col { position: absolute; right: 0px; top: 0px;width: fit-content; height: 100%; padding: 10px 21px; text-align: right; opacity: 0;transition: 0.4s ease-out all;}
.project_colanimate .col::after,.project_colanimate .col::before{content:'';position: absolute;width: 15px;height: 15px;border: 1px solid var(--white);transition: 0.2s ease all;}
.project_colanimate .col::after{ top: 0;left: 0;border-right: none;border-bottom: none;}
.project_colanimate .col::before{bottom: 0;left: 0;border-right: none;border-top: none;}
.project_colanimate .col.open{opacity: 1;}


@media only screen and (max-width: 1750px) {
    .banner{min-height: 100vh;}
    .banner .left_col{width: calc(100% - 680px);}
    .banner .btn { right: -180px; }
}

@media only screen and (max-width: 1599px) {
    .banner .left_col{width: calc(100% - 620px);}
    .banner .left_col p br{display: none;}
}
@media only screen and (max-width: 1399px) {
    .banner p { max-width: 600px; }

}
@media only screen and (max-width: 1279px) {
    .banner .right_col { width: 100%; padding-top: 0; }
    .banner .left_col { width: 100%; }
    .banner .wrapper { flex-wrap: wrap-reverse; }
    .banner .client_plateform { margin-right: auto; margin-left: 0;margin-bottom: 15px; }
    .banner { min-height: auto; padding-bottom: 50px; }
    .banner .btn { right:20px; }

   
}
@media only screen and (max-width: 1023px) {
    .banner h1{letter-spacing: normal;}
    .banner .btn { right: 10px; bottom: 27px; }
    .banner .client_plateform { margin-top: 30px; }
    .banner .left_col { width: 100%; padding-right: 180px; }
   
}
@media only screen and (max-width:767px) {
    .banner { padding-top: 100px; min-height: auto; padding-bottom: 27px; min-height: calc(100vh - 62px);}
    .banner .client_plateform { margin-top: 15px; }
    .banner h1 br{ display: none;}
    .banner .left_col {padding-right:0; }
    .project_colanimate h3 { font-size: 22px; line-height: 24px; margin-bottom: 4px; }
    .project_colanimate h4 { font-size: 14px; line-height: 16px; }
    .project_colanimate { min-height: 69px; }
    .banner h1 { margin-bottom: 12px; font-size: 32px; }
    .banner .btn { right: auto; bottom: 0px !important; width: 110px; height: 110px; font-size: 12px; line-height: 16px; padding-top: 26px; position: relative;margin-top: 10px;}
    .banner p { max-width:100%; font-size: 14px; line-height: 1.4; }
    .banner p br{display: none;}
    .project_colanimate { min-height: 64px; margin-bottom: 40px; }
    .banner .project_colanimate .col::after,
    .banner .project_colanimate .col::before{display: none;}
    .banner .project_colanimate {display: none !important;}
    .banner:after { content: ''; position: absolute; left: 0; top: 0; width: 100%; height: 100%; background-color: #000; opacity: 0.1; }


}