.vision_video{height:920px;position: relative;overflow: hidden;}
.vision_video iframe{    pointer-events: none; position: absolute; top: 0; position: absolute; top: 50%; left: 50%; min-height: 145%; min-width: 110%; transform: translate(-50%, -50%);}
.vision_video .music { position: absolute; bottom: 25px; right:25px; cursor: pointer;z-index: 99;}
.vision_video .music.mutevideo:after { content: ''; position: absolute; top: 0; width: 3px; height: 50px; background-color: #000; left: 0; right: 0; margin: auto; bottom: 0; transform: rotate(45deg); }
.vision_video .play_pause{position: absolute; width: 86px; height: 86px; background-color:#f0f0f0; z-index: 99; bottom: 30px; right: 120px; border-radius: 50%; display: flex; justify-content: center; align-items: center;}
.vision_video .play_pause img{filter: brightness(0) saturate(100%) invert(0%) sepia(100%) saturate(7500%) hue-rotate(210deg) brightness(88%) contrast(115%);    width: 30px;}



@media only screen and (max-width: 1750px) {
    .vision_video{height:calc(100vh - 80px);}
}
@media only screen and (max-width: 1279px) {
    .vision_video{height: 550px;}
}
@media only screen and (max-width: 1023px) {
    .vision_video{height:480px;}
}
@media only screen and (max-width:767px) {
    .vision_video{height:auto;} .vision_video iframe { left: 0; pointer-events: none; position: relative; top: 0; top: 0; transform: none; width: 100%; transform-origin: center; transform: scale(1.3); }
    .vision_video .music { display: block; bottom: 15px; right: 15px; width: 50px; }
    .vision_video .music.mutevideo:after { width: 2px; height: 32px; }
    .vision_video .play_pause{ width: 49px; height: 49px; bottom: 20px; right: 74px; }
    .vision_video .play_pause img{width: 18px;}
}
